var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"post",attrs:{"id":"kt_post"}},[_vm._ssrNode("<div class=\"mb-10\"><div class=\"mb-0\"><div class=\"d-flex align-items-center mb-4\"><h1 class=\"fs-2 fw-bolder text-gray-900 text-hover-primary me-1\">\n          Kobi Sipariş nasıl çalışır?\n        </h1></div> <div class=\"fs-base fw-normal text-gray-700 my-4 docText\">\n        Kobiye ait oluşturulan tüm Siparişler burada listelenir. Alınan Ürün\n        ve Satılan ürün olarak ayrılmışlardır. Üst tarafta Genel Bakiye, Kalan\n        Ödemeler ve Kalan Tahsilat göstergeleri bulunur. Genel Bakiye, tahsil\n        edilmiş tutarlardan ödenmiş tutarları çıkarılıp bunların toplamından\n        hesaplanır. Kalan Ödemeler, alınan ürünlerin Kalan Tutarları\n        toplamından hesaplanır. Kalan Tahsilatlar, satılan ürünlerin Kalan\n        Tutarları toplamından hesaplanır. Sağ üstten &quot;Ekle&quot; butonu ile ekleme\n        işlemi yapılabilir.\n      </div> <div class=\"fs-base fw-normal text-gray-700 my-4 docText\">\n        Ekleme menüsünde; Cari Hesap, Ürün Adı, Alış-Satış seçimi, Tutar, KDV,\n        KDV Tutarı, KDV Dahil, Vergili Ödenecek Tutar, Taksit Sayısı, Taksit\n        Tutarı, Vade Tarihi, Ödenen Tutar ve Etiket kolonları bulunur. Zorunlu\n        alanlar <span class=\"text-danger\">*</span> ibaresiyle gösterilmiştir.\n        Taksit sayısı girilirse &quot;Vergili Ödenecek Tutar&quot; ilgili taksit\n        sayısına bölünerek Taksit Tutarı kolonunda hesaplanır. Ayrıca Ödenen\n        Tutar girilirse altında &quot;Bankadan Ödeme&quot; ve &quot;Kasadan Ödeme&quot; butonları\n        aktif olur.\n      </div> <div class=\"fs-base fw-normal text-gray-700 my-4 docText\">\n        Eğer taksit bilgisi girilmediyse verilerin sağında &quot;Ödeme&quot; butonu\n        aktif olur. Bu butona tıkladığımızda karşımıza çıkan sayfada ürünün\n        bilgileri gösterilir. Hemen altında &quot;Banka Ödeme&quot; ve &quot;Kasa Ödeme&quot;\n        butonları vardır. Biri seçildikten sonra &quot;Ödenecek Tutar&quot; girilir. Ya\n        da istenilirse &quot;Tamamını Öde&quot; butonu ile tüm tutar bilgisi otomatik\n        girilebilir. Ödenen tutar &quot;Ödenmiş Tutar&quot; altında gösterilir. Ödeme\n        yapıldıktan sonra eğer tamamı ödendi ise &quot;Ödeme&quot; butonu kaybolur.\n      </div> <div class=\"fs-base fw-normal text-gray-700 my-4 docText\">\n        Taksik bilgisi girildiyse eğer verilerin sağında &quot;Taksit&quot; butonu aktif\n        olur. Örneğin; 1000₺'lik bir ürün için taksit sayısı 4 ise &quot;Taksit&quot;\n        butonu 4 kez aktif olur. İlk başta &quot;1. Taksit&quot; butonu oluşur.\n        Tıklanıldığında Banka ya da Kasa seçimi sunulur. Seçildiğinde ise 250₺\n        Banka ya da Kasadan alınır. Son taksit ödenince Ödenmiş Tutar, Vergili\n        Ödenecek Tutar ile eşitlenir ve buton kaybolur.\n      </div></div> <div class=\"separator separator-dashed my-8\"></div></div> "),_vm._ssrNode("<div class=\"d-flex justify-content-between mb-0\">","</div>",[_c('router-link',{staticClass:"btn btn-light btn-active-light-primary fw-bold fs-6 mx-2",attrs:{"to":"/docs/kobi/cari"}},[_vm._v("Önceki\n    ")]),_vm._ssrNode(" "),_c('router-link',{staticClass:"btn btn-light btn-active-light-primary fw-bold fs-6 mx-2",attrs:{"to":"/docs/kobi/kasa"}},[_vm._v("\n      Sonraki")])],2)],2)
